import uniStep from '@/componets/uni/step/uni-steps.vue'
export default {
  name: 'orderBackProgress',
  components: {
	  uniStep
  },
  data () {
    return {
      list: null,
      tempItem: null,
	  stepsData: []
    }
  },
  methods: {
    openInfo (item) {
      item.opened = !item.opened
    },
    getBackPay (item) {
      switch (item.refund_pay_type) {
        case 'alipay':
          return '支付宝'
        case 'wechatpay':
		case 'miniapp':
		  return '微信'
		default:
		  return '其它'
      }
    },
    getData () {
      this.$http(this.$urlPath.orderAfterSalesLog, {
        ord_id: this.$routeParams.id
      }, '', (data) => {
        if (data.data && data.data instanceof Array) {
          this.list = data.data
          this.list.forEach(item => {
            this.$set(item, 'opened', false)
            switch (item.status) {
              case 'REFUND_STATUS':
                item.stateTip = '退款审核中'
                break
              case 'REFUND_STATUS_YES':
                item.stateTip = '审核成功'
                break
              case 'REFUND_STATUS_NO':
                item.stateTip = '审核失败'
                break
              case 'REFUND_STATUS_CANCEL':
                item.stateTip = '取消退款'
                break
              case 'REFUND_PAY':
                item.stateTip = '待退款'
                break
              case 'REFUND_PAY_YES':
                item.stateTip = '退款完成'
                break
              case 'REFUND_PAY_NO':
                item.stateTip = '退款失败'
                break
            }
					  // [
			// 					{title:'买家下单',desc:'2018-11-11'},
			// 					{title:'卖家发货',desc:'2018-11-12'},
			// 					{title:'买家签收',desc:'2018-11-13'},
			// 					{title:'交易完成',desc:'2018-11-14'}]
            item.refund_log.forEach(item => {
				let step = {}
				step.desc = item.ctime
				switch (item.type) {
					case 1:
						step.title = '提交退款申请'
						break
					case 2:
						step.title = '退款审核通过'
						break
					case 3:
						step.title = '退款审核失败'
						break
					case 4:
						step.title = '退款成功'
						break
					case 5:
						step.title = '取消退款成功'
						break
              }
			  this.stepsData.push(step)
            })
          })
        }
      }, (errorCode, error) => {
        this.$toast(error)
      })
    },
    cancelBack (item) {
      this.tempItem = item
      this.$refs.confirmDialog.showDialog()
    },
    dialogConfirm () {
      if (this.tempItem) {
        this.$http(this.$urlPath.orderCancelRefund, {
          rid: this.tempItem.rid
        }, '正在取消…', (result) => {
          this.$root.$emit('onReload')
          this.$toast('取消退款成功')
          this.getData()
        }, (errorCode, error) => {
          this.$toast(error)
        })
      }
    }
  },
  onLoad () {
    this.getData()
  }
}
